.chitiet-index1
	background-color: #f3f6fb
	padding-top: 30px

	.title-main
		color: $blue
		padding: 10px 20px
		border-bottom: 1px solid #cccccc
	
	.bg-white
		background-color: #ffffff !important
		border-radius: 15px
	.box-left
		position: relative
		
		padding: 20px

		border-right: 1px solid #cccccc
		
		.sale
			position: absolute
			left: 20px
			top: 15px
			ul
				li
					padding: 15px 10px
					font-size: 11px
					font-weight: bold
					color: $white
					text-transform: uppercase
					margin-bottom: 20px
					border-radius: 0 0 50px 50px
		.side-top
			position: relative
			padding: 30px 0

			.img
				width: 100%
				height: 275px
				display: flex
				align-items: center
				justify-content: center
				img
					
					object-fit: contain
			.main-button
				@media(max-width: 576px)
					display: flex
					align-items: center
					justify-content: center
					padding-top: 20px
				.button-next,.button-prev 
					width: 50px
					height: 50px
					border-radius: 50%
					border: 1px solid #d8d8d8
					display: flex
					justify-content: center
					align-items: center
					z-index: 99
					position: absolute
					top: 50%
					@media(max-width: 576px)
						position: static
						
						

					&:hover
						background-color: $red
						cursor: pointer
						i
							color: white
				.button-next
					right: 20px
					@media(max-width: 576px)
						margin-left: 20px
				.button-prev 
					left: 20px

	

		.side-thumbs
			margin: 20px

			.img
				width: 90px
				height: 90px
				padding: 8px
				border: 1px solid #cccccc
				display: flex
				justify-content: center
				align-items: center
				img
					object-fit: contain
					width: 100%
					height: 100%
		.adv-section
			
			.img
				width: 100%
				
				img
					object-fit: contain
					width: 100%
					height: 100%
	.box-center
		@media(max-width: 768px)
			padding: 20px
		.header
			border-bottom: 1px solid #999999
			padding-top: 20px
			.review
				display: flex
				align-items: center
				ul
					display: flex
					li
						display: flex
						i
							color: #ffc120
							font-size: 13px
						.comment
							font-size: 14px
							color: #999999
			.info-sp
				font-size: 14px
				display: flex
				flex-wrap: wrap
				padding-top: 10px
				.item
					flex: 0 0 50%
					display: flex
					margin-bottom: 10px
					p
						font-weight: 600
					span 
						padding-left: 10px

					i
						color: #cccccc
						font-size: 24px
				
		.body
			.ct-top
				font-size: 14px
				padding: 20px 0
				.item
					display: flex
					position: relative
					padding-left: 20px
					margin-bottom: 5px
					p
						font-weight: 600
					span
						padding-left: 15px
					&::before
						content: ""
						width: 5px
						height: 5px
						position: absolute
						background-color: #999999
						border-radius: 50%
						top: 7px
						left: 0px
			.ct-price
				display: flex
				border-top: 1px solid #cccccc
				padding: 20px
				flex-wrap: wrap
				
				background-color: #f3f6fb
				.price	
					
					font-size: 24px
					color: $red
					font-weight: 600
				
				
				.old-price
					font-size: 14px
					padding-left: 10px
					padding-top: 10px
					text-decoration: line-through
				

					color: #999999
				.recon 
					flex: 0 0 100%
					font-size: 12px
					span
						color: #1e4488
			.number
				display: flex
			
				margin-top: 20px
				align-items: center
				.input-quality
					display: flex
					align-items: center
					justify-content: center
					border-radius: 0 0 39px 39px
					overflow: hidden
					max-width: 130px
					border: 1px solid #cccccc
					height: 40px
				.title
					font-size: 14px
					text-transform: uppercase
					padding-right: 20px
					font-weight: 500
					
				.btn-desc, .btn-inc
					width: 33px
					height: 100%
					background-color: #f8f8f8
					
					display: flex
					justify-content: center
					align-items: center
					min-width: 33px
				input
					width: 100%
					border: none
					outline: none
					height: 100%
					text-align: center
					padding: 5px
			.add-cart
				display: flex
				justify-content: space-between
				
				align-items: center
				padding: 20px 0
				border-bottom: 1px solid #999999
				.tab-button
					ul
						li
							background-color: $red
							padding: 10px 20px
							border-radius: 0 0 39px 39px
							
							display: flex
							align-items: center
							&:hover
								opacity: 0.8
							
							i
								color: $white
							a
								color: $white
								padding-left: 10px
								font-size: 14px
								text-transform: uppercase
								font-weight: 600
				.heart
					i
						color: $red
						font-size: 24px
						&:hover
							opacity: 0.8



	
		.footer
			.social
				display: flex
				
				padding-top: 20px
				align-items: center
				p
					font-size: 14px
					text-transform: uppercase
					padding-right: 20px
					color: #333333
					font-weight: bold
					
			ul
				padding-left: 20px

				li
					font-size: 14px
	.side-right
		padding: 20px
		.wrap-top
			ul
				box-shadow: 0 0 10px 0 rgba(0,0 ,0 ,0.2 )
				li
					display: flex
					padding: 20px
					border-bottom: 1px dashed #cccccc
					i
						color: $blue
						padding-right: 20px
					p
						font-size: 13px
		.adv-section
			padding-top: 30px
			.img
				img
					width: 100%
					height: 100%

		
	
