.order-info
	background-color: #f3f6fb
	padding: 30px 0
	> .container 
		>.row
			margin-bottom: -30px
			>*
				margin-bottom: 30px
	@media(max-width: 576px)
	
		margin-top: 30px
	.info-cutomer
		background-color: $white
		border-radius: 15px
	
		

	.headtitle
		font-size: 24px
		color: $blue
		font-weight: 600
		border-bottom: 1px solid #cccccc
		padding: 20px
		@media(max-width: 576px)
			font-size: 20px
	.orther
		p
			font-size: 16px
			color: #929292
		a
			color: #068fdd
			font-size: 16px
			padding-left: 20px
			text-decoration: underline

	.section-billred
		background-color: $white
		border-radius: 15px
		margin-top: 30px
	.note
		p
			font-size: 14px
			color: #999999
		a
			color: #068fdd
			text-decoration: underline
			font-weight: 500
	.voucher
		background-color: $white
		border-radius: 15px
		.input-code
			padding: 30px 20px
			
			position: relative
			input
				padding: 10px
				border-radius: 15px
				border: 1px solid #cccccc
				width: 100%
				

			.btn
				background-color: $blue
				padding: 12px
				border-radius: 0 15px 15px 0 
				position: absolute
				top: 30px
				right: 20px
				font-size: 14px
				text-transform: uppercase
				color: $white
		.warning
			background-color: #e5f7ec
			padding: 20px
			p
				font-size: 14px
				color: #63b53d

	.section-cart
		background-color: $white
		border-radius: 15px
		margin-top: 30px
	
		.product
			display: flex
			justify-content: space-between
			padding: 10px 20px 10px 0
			border-bottom: 1px solid #cccccc
			align-items: center
		
			figure
				display: flex
				align-items: center
				.product-img
					width: 95px
					min-width: 95px
					height: 95px
					img 
						width: 100%
				.product-name
					font-size: 12px
				.removeproduct
					font-size: 14px
					display: flex
					color: #cccccc
					em
						padding-right: 10px
		.price
			width: 50%
			.top
				font-size: 14px
				text-align: right
			.mid
				font-size: 14px
				text-align: right
			.bottom
				font-size: 14px
				color: $blue
				font-weight: bold
				text-align: right
		.total
			padding: 20px
			border-bottom: 1px solid #cccccc
			.totalamount, .total-ship, .price-sale 
				font-size: 14px
				font-weight: 600
				display: flex
				padding-bottom: 10px
				justify-content: space-between
				span
					font-weight: 400
		.all-total 
			font-size: 14px
			font-weight: 600
			padding: 20px
			display: flex
			padding-bottom: 10px
			justify-content: space-between
			align-items: center
			span 
				font-size: 18px
				color: $red
	.button-buy
		padding: 30px 0
		text-align: right
		.btn-continue
			background-color: $blue
			padding: 10px 20px
			color: $white
			text-transform: uppercase
			font-weight: 600
			
			border-radius: 0 0 39px 39px
			font-size: 14px
			position: relative
			&::before
				position: absolute
				content: "\EA6E"
				font-family: "remixicon"
				top: 50%
				right: 20px
				transform: translateY(-50%)

