.finish-bill
	background-color: #f3f6fb
	padding: 30px
	margin-top: 30px
	.finish-wrap
		
		background-color: $white
		border-radius: 15px
		.aler-finish
			display: flex
			align-items: center
			background-color: #e5f7ec
			border-radius: 15px 15px 0 0
			padding: 10px 20px

			i
				font-size: 30px
				color: #63b53d
			p
				font-size: 24px
				color: #63b53d
				font-weight: 600
				@media(max-width: 576px)
					font-size: 20px
		.content
			padding: 30px
			font-size: 14px
			.img
				width: 100%
				img
					width: 100%
			.heading
				font-size: 20px
				color: $blue
				@media(max-width: 576px)
					font-size: 16px
			.title2
				font-size: 14px
				span
					font-weight: bold
					font-size: 20px
			.info
				font-size: 14px
				span
					color: $red
				p
					color: $blue
		.contact
			background-color: #f3f6fb
			border-top: 1px solid #cccccc
			margin-top: 20px
			padding: 20px
			margin-bottom: 20px
			strong
				font-size: 14px

			li
				display: flex
				font-size: 14px
				padding: 10px
				
				p
					color: $blue
					font-weight: 600
				a
					padding-left: 30px
			

