.header-index7
	padding-top: 30px
	padding-bottom: 30px
	.slide
		margin: 0 auto
	
		.img
			margin: 0 auto
			width: 100%
			height: 150px
			background-color: $white
			position: relative
			display: flex
			justify-content: center
			align-items: center
			padding: 10px
			img 
				max-width: 100%
				max-height: 100%
				object-fit: contain
		.button-next,.button-prev 
			width: 50px
			height: 50px
			border-radius: 50%
			border: 1px solid #cccccc
			display: flex
			justify-content: center
			align-items: center
			cursor: pointer
			background-color: #fff
			&:hover
				background-color: $red
				i
					color: $white

			i
				color: #cccccc
				font-size: 24px
		.button-next
			position: absolute
			top: 50%
			transform: translateY(-50%)
			right: -60px
			z-index: 99
		.button-prev
			position: absolute
			top: 50%
			transform: translateY(-50%)
			z-index: 11
			left: -67px
		@media screen and ( max-width: 780px )
			.button-next, .button-prev
				position: static 
				top: auto 
				right: auto 
				left: auto
				bottom: auto 
				transform: none 
				margin: 0 10px
			.main-button 
				display: flex
				justify-content: center
				align-items: center
				margin-top: 20px
