.side-bar
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2)
	padding-bottom: 30px
	margin-bottom: 30px
	
	
	.sub-nav
		display: none
	.down-content
		display: none
	.title
		display: flex
		align-items: center
		padding: 20px 0
		border-bottom: 1px solid #e1e1e1
		
		h3
		font-size: 16px
		text-transform: uppercase
		color: #1e4488
		
		.img
			img
				padding: 0 20px

	.list-cata
		> ul
			list-style: none
			padding: 0
			
			> li
				
				> a
					padding: 10px 20px
					font-size: 14px
					color: #333333
					display: inline-flex
		
		.has-dropdown
			position: relative
			padding: 0	
			&.active
				em
					transform: rotate(180deg)
					color: $red
					transition: .2s ease-in-out all


			em
				position: absolute
				top: 5px
				right: 20px
				cursor: pointer
				font-size: 20px
				
		
	.sub-nav
		background-color: #f8f8f8
		margin: 0
		padding: 0
		list-style-type: disc
		padding-left: 40px
		display: none
		li
			
			position: relative		
			a
				padding: 10px 20px
				padding-left: 10px
				display: inline-flex 
				font-size: 14px
	
	.content
		padding: 20px
		display: none
		.filter-price 	
			display: flex
			align-items: center
			font-size: 14px
			padding: 10px
			span
				color: $red
				padding: 10px
				font-weight: 500

	.slider-range
		
		height: 3px
		width: 100%
		.ui-state-default
			border-radius: 50%
		.ui-widget-header
			background-color: $red
			height: 2px
		.ui-slider-handle
			width: 14px
			height: 14px
			margin: auto
			background: $red
			&:hover
				border: none
			&:focus 
				outline: none 
			// position: relative
			&:before 
				content: ''
				width: calc(100% - 1px)
				height: calc(100% - 1px)
				border-radius: 50%
				position: absolute
				top: 50%
				left: 50%
				transform: translate(-50%, -50%)
				z-index: 11
				border: 2px solid $white
		.ui-widget.ui-widget-conten
			border: none
	.wrap-form
		padding-top: 20px
	
		ul 
			padding-left: 20px
			 
			li 
				
				display: flex
				align-items: center
				padding-left: 20px
				padding-bottom: 10px

				a 
					position: relative
					font-size: 14px
					&:before 
						font-family: 'Material Icons'
						content: 'check_box_outline_blank'
						position: absolute
						top: 0
						left: -20px
				&.active 
					a 
						&:before 
							content: 'check_box'
							color: $red
	.df
		display: flex
		flex-wrap: wrap
		li
		
			flex: 0 0 50%
			max-width: 50%
			width: 100% 

	.more
		border-top: 1px solid #9999
		padding-top: 20px
		position: relative
		&::before
			position: absolute
			content: "arrow_drop_down"
			font-family: "material icons"
			top: 16px
			right: 72px
			font-size: 20px
			color: #999999
		.title2
			text-align: center
			font-size: 14px
			color: #999999
			font-weight: bold
			text-transform: uppercase
	.has-dropdown2
		position: relative
	
			
		
		em
			position: absolute
			top: 20px
			right: 15px
			cursor: pointer
			font-size: 20px
			font-weight: bold
			&.active-xoay
				transform: rotate(180deg)
				color: $red
				transition: .2s ease-in-out all
		
	.heading
		display: flex
		justify-content: space-between
		align-items: center
		padding-bottom: 30px
	.button
		background: $blue
		padding: 10px 30px
		border-radius: 10px
		color: $white
		font-size: 14px
		text-transform: uppercase
		font-weight: bold
