.chitiet-index2
	padding-top: 40px
	background-color: #f3f6fb
	>.container
		>.row
			margin-bottom: -30px
			>*
				margin-bottom: 30px
	
	.bg-white
		background-color: #ffffff !important
		border-radius: 15px
	
	.title-main
		font-size: 24px
		color: $blue
		padding: 20px
	.full-content 
		border-top: 1px solid #cccccc
		font-size: 16px
		padding: 20px
		p
			img
				padding: 10px 0
	
	.button-more
		font-size: 14px
		color: $blue
		text-transform: uppercase
		font-weight: bold
		text-align: center
		padding-bottom: 10px
		i
			color: $blue
	.product-item
		margin: 0
	.section-cmt
		
		margin-top: 50px
		.comment-form
			border-top: 1px solid #cccccc
		.journalTools
			border-bottom: 1px solid #cccccc
			margin: 20px
			padding-bottom: 20px
		.journalEditor
			

			border-radius: 15px
			border: 1px solid #cccccc
			.journalContent
				width: 100%
				padding: 20px
				height: 145px
				border: 0
				outline: none
				border-radius: 15px
			.journalInfo
				border-top: 1px solid #cccccc
				padding: 10px
				display: flex
				align-items: center
				flex-wrap: wrap
				input
					border: none
					outline: none
					background-color: #f4f4f4
					padding: 10px
					border-radius: 50px
					font-size: 12px
					color: #999999
					margin-right: 10px
					@media(max-width: 768px)
						margin: 10px
				.btn-share
					background-color: #f4f4f4
					padding: 10px 20px
					border-radius: 20px
					text-transform: uppercase
					font-weight: bold
					font-size: 12px
					
				.rating
					display: flex
					padding: 0 20px
					.title
						font-size: 14px
						color: #999999
						padding-right: 10px
					ul
						display: flex
						li

							i
								color: #ffc120
								font-size: 12px
	.comment-list
		.heading
			font-size: 16px
			color: $blue
			text-transform: uppercase
			font-weight: bold
			padding: 20px
		.cmt-item
			padding:  0 20px
		.comment-items
			display: flex
			align-items: center
			border-top: 1px solid #cccccc
			border-bottom: 1px solid #cccccc
			padding: 20px 0
		.user-content
			padding-left: 20px
			.name
				font-size: 16px
				display: flex
				align-items: center
				p
					color: $blue
					font-weight: bold
					margin-right: 10px
				.mod
					border-radius: 5px
					background-color: #f4f4f4
					padding: 10px
					font-size: 12px
					
					color: #999999
			.rating
				display: flex
				
				.title
					font-size: 14px
					color: #999999
					padding-right: 10px
				ul
					display: flex
					li

						i
							color: #ffc120
							font-size: 12px
			.content
				font-size: 16px
				color: #333333
			.reply-wrapper
				font-size: 14px
				a
					color: #66b2f6
					padding-right: 10px
				span
					font-size: 14px
					color: #999999
	.jcmt
		margin-left: 90px
		.cmt
			padding: 20px 0
			display: flex
			align-items: center
			.jsummary
				padding-left: 20px
				.name
					font-size: 16px
					display: flex
					align-items: center
					p
						color: $blue
						font-weight: bold
						margin-right: 10px
					.mod
						border-radius: 5px
						background-color: #f4f4f4
						padding: 10px
						font-size: 12px
						
						color: #999999
				.desc
					font-size: 16px
					color: #333333
				.reply-wrapper
					font-size: 14px
					a
						color: #66b2f6
						
					span
						font-size: 14px
						color: #999999
						padding-left: 10px
		.cmteditarea
			width: 100%
			padding-top: 20px
			.cmteditor
				width: 100%
				height: 145px
				border: 1px solid #cccccc
				border-radius: 5px
		.cmtbtn
			padding: 10px 0
			@media(max-width: 768px)
				padding-bottom: 20px
			.cmtinfo
				input
					border: none
					outline: none
					background-color: #f4f4f4
					padding: 10px
					border-radius: 5px
					font-size: 12px
					color: #999999
					margin-right: 10px
					@media(max-width: 768px)
						margin: 10px
				.btn-share
					background-color: #f4f4f4
					padding: 10px 20px
					border-radius: 5px
					text-transform: uppercase
					font-weight: bold
					font-size: 12px	
					@media(max-width: 768px)
						margin: 0 10px
					
