.header-index1
	padding: 30px 0
	.feature-item
		display: flex
		box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2)
		border-radius: 50px
		align-items: center
		margin-bottom: 30px
		&:hover
			background-color: $red
			.info
				color: $white
			.icon
				i 
					color: red

		.icon
			background-color: #f8f8f8
			width: 50px
			height: 50px
			border-radius: 50%
			display: flex
			justify-content: center
			align-items: center
			i
				color: $blue
				margin: auto
				font-size: 14px
		.info
			font-size: 14px
			color: #333333
			
			margin-left: 10px

	@media(min-width: 1280px)
		.col-xl-5-custom
			flex: 0 0 20%
			max-width: 20%
