.login-panel
	background-color: #f3f6fb
	padding: 30px 0
	margin-top: 30px
	.login-form
		background-color: $white
		border-radius: 10px
		width: 560px
		margin: 0 auto
		@media(max-width: 768px)
			width: 100%
				

		.headtitle
			font-size: 24px
			color: $blue
			padding: 10px 20px
			border-bottom: 1px solid #cccccc
		.select-group
			padding: 20px
			font-size: 14px
			li 
				display: flex 
				align-items: center
				input 
					display: none
				label 
					padding-left: 20px
					position: relative
					&:before 
						content: 'radio_button_unchecked'
						font-family: 'Material Icons'
						position: absolute
						top: 0
						left: 0
						z-index: 11
				input 
					&:checked
						~ label 
							color: $blue
							&:before 
								content: 'radio_button_checked'
								color: $red
		.form-group
			padding: 10px 20px

			input[type='text']
				width: 100%
				padding: 13px 20px
				border: 1px solid #cccccc
				&:placeholder
					color: #999999
					font-size: 16px
					font-weight: 300

			input[type='submit']
				background-color: $blue
				border: none
				padding: 10px 20px
				width: 100%
				border-radius: 0 0 39px 39px
				color: $white
				font-weight: 600
				text-transform: uppercase

		.btn-group
			padding-left: 20px
			.qmt
				color: #00addf
				font-size: 14px
			.dk
				font-size: 14px
				color: #1e4488
				padding-left: 20px
		.bnt-social
			width: 100%
			display: flex
			justify-content: space-between
			padding: 20px
			.button
				flex: 0 0 50%
				text-transform: uppercase
				font-size: 14px
				font-weight: 600
				padding-right: 20px
				&:last-child
					padding-right: 0
				&-blue
					color: $blue
					
					i
						padding-right: 10px
				&-red
					color: $red
					i
						padding-right: 10px

			.fb,.gg
				display: flex
				align-items: center
				padding: 15px
				background-color: #f3f3f3
				border-radius: 0 0 39px 39px
				justify-content: center
			
			
				
				



