.header-cartpage 
	padding: 20px
	position: fixed
	z-index: 999
	width: 100%
	background-color: $white
	left: 0
	right: 0
	top: 0

	.header-cartwrap
		display: flex
		align-items: center
		justify-content: space-between
		// flex-direction: row
		@media(max-width: 768px)
			flex-direction: column
			align-items: center

		
		.right-pm
			display: flex
			justify-content: space-between
			position: relative
			width: 100% 
			max-width: 800px
		
			&::before
				position: absolute
				top: 30%
				left: 10%
				transform: translateY(-50%)
				z-index: -1
				border: thin dashed #e1e1e1
				width: 75.314685314%
				height: 1px
				content: ""
		.finish
			.name
				color: #63b53d !important
				font-weight: bold
			.step-count
				background-color: #63b53d !important
				
				position: relative
				display: flex
				align-items: center
				font-size: 0
				&::before
					position: absolute
					content: "\EB7B"
					font-family: "remixicon"
					display: flex
					align-items: center
					justify-content: center
					color: $white
					font-size: 18px

		.step-pm
			display: flex
			flex-direction: column
			justify-content: space-between
			align-items: center
			flex: 0 0 180px
			.step-count
				background-color: #fafafa
				border-radius: 50%
				width: 50px
				height: 50px
				display: flex
				align-items: center
				justify-content: center
				color: #1e4488
				font-weight: bold
				border: 1px solid #cccccc
			.name
				font-size: 16px
				color: #999999
			&.active
				.step-count
					background-color: #1e4488
					color: $white
				.name
					color: #1e4488
					font-weight: bold

	@media(max-width: 768px)
	
		.right-pm
			margin-top: 30px
			width: 100% 
			max-width: 100%
			padding-bottom: 20px
			@media(max-width: 576px)
				overflow-x: auto
				overflow-y: hidden
				&::-webkit-scrollbar
					width: 6px
					cursor: pointer
					height: 3px
				&::-webkit-scrollbar-track 
					background: #f1f1f1 
				/* Handle */
				&::-webkit-scrollbar-thumb 
					background: $blue
				

				/* Handle on hover */
				&::-webkit-scrollbar-thumb:hover 
					opacity: 0.8
			&::before
				display: none
