.mail
	
	background-color: #00225e
	.mail-wrap
		display: flex
		padding: 20px 0
		justify-content: space-between
		align-items: center
		@media screen and ( max-width: 1280px)
			flex-direction: column
		.left
			display: flex
			align-items: center
			color: $white
			i
				font-size: 48px
		.info
			padding-left: 20px	
			.title
				font-size: 18px
				text-transform: uppercase
			
			.span
				font-size: 16px
	.subscribefrm
		position: relative
		width: 600px
		@media screen and ( max-width: 680px )
			width: auto
			
		input
			width: 100%
			padding: 10px
			border-radius: 6px
		button
			position: absolute
			top: 1px
			right: 0px
			background-color: #cccccc
			border: none
			outline: none
			padding: 10px
			width: 30%
			border-radius: 0 6px 6px 0
			color: $blue
			font-weight: bold
