.sanpham2
	.heading-main
		display: flex
		align-items: center
		justify-content: space-between
		flex-wrap: wrap
	.mt
		margin-top: 30px
	.title-main
		font-size: 30px
		font-weight: 600
	.wrap-form
		display: flex
		align-items: center
		
		.form-group
			display: flex
			align-items: center
			
			&:last-child
				margin-right: 0
			label
				font-size: 14px
				color: $blue
				font-weight: bold
				text-transform: uppercase
				margin-right: 20px
			select
				width: 80%
				padding: 10px
				color:#999999
				background-color: #f8f8f8
				border: 1px solid #e1e1e1
				border-radius: 5px
	.pagination
		display: flex
		align-items: center
		.page-item
			display: flex
			align-items: center
			background-color:#f8f8f8
			padding: 20px
			border-radius: 50%
		.page-link 
			
	@media(max-width: 576px)
		.title-main
			font-size: 20px
		
