.header-index6
	padding-top: 30px
	.title-main
		color: $white
	.content
		color: $white
		max-width: 750px
		margin: auto
		padding-bottom: 30px
		text-align: center

		.title
			span
				font-weight: bold
	
	.slide
		max-width: 835px
		width: 100% 
		
		position: relative
		margin: 0 auto
		padding-bottom: 50px
	
		@media screen and ( max-width: 1980px)
			overflow: visible
		@media screen and ( max-width: 780px)
			max-width: 680px
			width: 100%
			overflow: hidden
			margin: 0 auto 
		
			
		
		.img
			margin: 0 auto
			width: 150px
			height: 150px
			background-color: $white
			border-radius: 50%
			position: relative
			display: flex
			justify-content: center
			align-items: center
			padding: 10px
			// @media screen and ( max-width: 780px)

			// 	width: 190px
			// 	height: 190px
				
			img 
				max-width: 100%
				max-height: 100%
				object-fit: contain
		.button-next,.button-prev 
			width: 50px
			height: 50px
			border-radius: 50%
			border: 1px solid #cccccc
			display: flex
			justify-content: center
			align-items: center
			cursor: pointer
			background-color: #fff
			&:hover
				background-color: $red
				i
					color: $white

			i
				color: #cccccc
				font-size: 24px
		.button-next
			position: absolute
			top: 40%
			transform: translateY(-50%)
			right: -60px
			z-index: 99
		.button-prev
			position: absolute
			top: 40%
			transform: translateY(-50%)
			z-index: 11
			left: -67px
		@media screen and ( max-width: 780px )

			.button-next, .button-prev 
				position: static 
				top: auto 
				right: auto 
				left: auto
				bottom: auto 
				transform: none 
				margin: 0 10px
			.main-button 
				display: flex
				justify-content: center
				align-items: center
				margin-top: 20px
