.wrap-form
	// margin-top: 30px
	padding-top: 30px
	padding: 20px
	@media(min-width: 1025px)
		
		margin-top: 0
	.form-group
		padding-bottom: 20px
		display: flex
		justify-content: space-between
		align-items: center

		p
			color: #999999
			font-size: 14px
	label
		font-size: 16px
		color: #333333
		@media(max-width: 576px)
			padding-bottom: 10px
	input[type="checkbox"]
		display: none
		
	input[type='text'], textarea, select
		width: 75%
		padding: 10px 20px
		font-size: 15px
		border: 1px solid #e5e5e5
		color: #333333
		background-color: #fafafa
		border-radius: 5px
		caret-color: $blue
		@media(max-width: 576px)
			width: 100%
	
	
		&:placeholder
			color: #999999
			font-size: 16px
			font-weight: 300
		&:focus
			outline: none
			border-color: $blue
			box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.3)
	textarea
		height: 75px
		color: #999999
	
	&>div
		display: flex
		flex-wrap: wrap
		margin: 0 -5px
		.qtext
			padding: 0 5px
			flex: 0 0 50%
			margin-bottom: 20px
			@media(max-width: 768px)
				flex: 0 0 100%
		.qparagraph
			flex: 0 0 100%
			padding: 0 5px
			margin-bottom: 20px
		span
			color: red
			font-size: 13px
	.frm-captcha
		display: flex
		align-items: center
		flex-direction: row-reverse
		justify-content: flex-end
		display: flex
		.frm-captcha-input
			width: calc(100% - 227px)
			label
				display: none
		.RadCaptcha
			width: auto!important
			position: relative
			span
				position: absolute
				bottom: 100%
			&>div>div
				align-items: center
				display: flex
				width: 227px
		.rcRefreshImage
			margin: 0 10px
			font-size: 0
			&:after
				content: url('/Data/Sites/1/media/change.png')
	.frm-btnwrap
		margin-top: 20px
		label
			display: none
		.frm-btn-reset
			display: none
		.frm-btn
			width: 100%
		input
			width: 100%
			padding: 13px 30px
			background: $main
			text-transform: uppercase
			color: #ffffff
			border: none
	@media(min-width: 768px)
		.frm-captcha
			width: 75%
			float: left
		.frm-btnwrap
			float: left
			margin-top: 0
			width: 25%
			margin-left: 10px
	.check-form
		font-size: 16px
		padding-top: 20px
		padding-left: 20px
		label
			position: relative
			&::before
				position: absolute
				content: "check_box_outline_blank"
				font-family: "material Icons"
				top: 0
				left: -20px
		input
			&:checked
				~label
					color: $blue
					&::before
						content: "check_box"
						color: $red


