
.breadcrumb-global 
	background-color: #f3f6fb
	
ol.breadcrumb
	display: flex
	padding: 10px 0
	align-items: center
	list-style: none
	padding-left: 0
	border-bottom: 1px solid #e1e1e1

	
	li
		display: flex
		position: relative
		align-items: center
		padding: 0 10px
		a
			color: #999999
			font-weight: 400
			font-size: r(15px)
			padding-left: 11px
			&.active
				color: #646262
		&:first-child
			padding-left: 0
			&::before
				display: none
		&::before
			font-family: 'material icons'
			content: 'chevron_right'
			font-size: r(18px)
			color: #999999
