.bill-infor
	background-color: #f3f6fb
	padding: 20px 0
	.method-ship
		background-color: $white
		border-radius: 15px
		margin-top: 30px
		.wrap-form2
			padding: 30px 20px
			.form-group
				padding-bottom: 20px
				display: flex
				justify-content: space-between
				align-items: center
				@media(max-width: 580px)
					flex-direction: column

				.check-radio
					padding-left: 20px
					input
						display: none
					label
						font-size: 16px
						color: #999999
						padding-left: 10px
						position: relative
						&::before
							position: absolute
							content: "radio_button_unchecked"
							top: 0
							left: -20px
							font-family: "material icons"
					input
						&:checked
							~label
								&:before 
									content: 'radio_button_checked'
									color: $red
		.form-select
			width: 65%
			select
				width: 100%
				padding: 10px 20px
				font-size: 15px
				border: 1px solid #e5e5e5
				color: #333333
				background-color: #fafafa
				border-radius: 5px
				caret-color: $blue

	.headtitle
		font-size: 24px
		color: $blue
		font-weight: 600
		border-bottom: 1px solid #cccccc
		padding: 20px
		@media(max-width: 576px)
			font-size: 20px
	
	.section-cart
		background-color: $white
		border-radius: 15px
		margin-top: 30px
	
		.product
			display: flex
			justify-content: space-between
			padding: 10px 20px 10px 0
			border-bottom: 1px solid #cccccc
			align-items: center
		
			figure
				display: flex
				align-items: center
				.product-img
					width: 95px
					min-width: 95px
					height: 95px
					img 
						width: 100%
				.product-name
					font-size: 12px
				.removeproduct
					font-size: 14px
					display: flex
					color: #cccccc
					em
						padding-right: 10px
		.price
			width: 50%
			.top
				font-size: 14px
				text-align: right
			.mid
				font-size: 14px
				text-align: right
			.bottom
				font-size: 14px
				color: $blue
				font-weight: bold
				text-align: right
		.total
			padding: 20px
			border-bottom: 1px solid #cccccc
			.totalamount, .total-ship, .price-sale 
				font-size: 14px
				font-weight: 600
				display: flex
				padding-bottom: 10px
				justify-content: space-between
				span
					font-weight: 400
		.all-total 
			font-size: 14px
			font-weight: 600
			padding: 20px
			display: flex
			padding-bottom: 10px
			justify-content: space-between
			align-items: center
			span 
				font-size: 18px
				color: $red
	.button-buy
		padding: 30px 0
		text-align: right
		.btn-continue
			background-color: $blue
			padding: 10px 20px
			color: $white
			text-transform: uppercase
			font-weight: 600
			
			border-radius: 0 0 39px 39px
			font-size: 14px
			position: relative
			&::before
				position: absolute
				content: "\EA6E"
				font-family: "remixicon"
				top: 50%
				right: 20px
				transform: translateY(-50%)
	.method-pay
		margin-top: 30px
		background-color: $white
		border-radius: 15px

		.wrap-form3
			.form-group
				padding: 20px 30px
				display: flex
				justify-content: space-between
				align-items: center

			
				input
					display: none
				label
					font-size: 16px
					color: #999999
					margin-left: 20px
					position: relative
					border: 1px solid #cccccc
					width: 100%
					padding: 20px
					.pay-item
						display: flex
						align-items: center
						.info
							font-size: 16px
							color: #333333
							padding-left: 30px
					&::before
						position: absolute
						content: "radio_button_unchecked"
						top: 50%
						transform: translateY(-50%)
						left: -30px
						font-family: "material icons"
				input
					&:checked
						~label
							&:before 
								content: 'radio_button_checked'
								color: $red
		.rule
			
			font-size: 16px
			text-transform: uppercase
			font-weight: 600
			padding-left: 20px
		.check-form
			font-size: 16px
			padding-top: 20px
			padding-left: 40px
			padding-bottom: 30px
			input
				display: none
			label
				position: relative
				&::before
					position: absolute
					content: "check_box_outline_blank"
					font-family: "material Icons"
					top: 0
					left: -20px
			input
				&:checked
					~label
						color: $blue
						&::before
							content: "check_box"
							color: $red

