.finish-bill2
	background-color: #f0f3f8
	.headtitle
		font-size: 24px
		color: $blue
		font-weight: 600
		border-bottom: 1px solid #cccccc
		padding: 20px
	.section-cart
		background-color: $white
		border-radius: 15px
		padding-bottom: 30px
		margin-bottom: 30px
		
	
		.product
			display: flex
			justify-content: space-between
			padding: 10px 20px 10px 0
			border-bottom: 1px solid #cccccc
			align-items: center
		
			figure
				display: flex
				align-items: center
				.product-img
					width: 95px
					min-width: 95px
					height: 95px
					img 
						width: 100%
				.product-name
					font-size: 16px
					font-weight: 500
					@media(max-width: 576px)
						font-size: 14px
				.removeproduct
					font-size: 14px
					display: flex
					color: #cccccc
					em
						padding-right: 10px
		.price
			@media(max-width: 576px)
				font-size: 14px
				width: 50%
			.top
				font-size: 16px
				text-align: right
				@media(max-width: 576px)
					font-size: 14px
			.mid
				font-size: 16px
				text-align: right
				@media(max-width: 576px)
					font-size: 14px
			.bottom
				font-size: 16px
				color: $blue
				font-weight: 500
				text-align: right
				@media(max-width: 576px)
					font-size: 14px
		.total
			padding: 20px
			border-bottom: 1px solid #cccccc
			.totalamount, .total-ship, .price-sale 
				font-size: 16px
				font-weight: 500
				display: flex
				padding-bottom: 10px
				justify-content: space-between
				@media(max-width: 576px)
					font-size: 14px
				span
					font-weight: 400
		.all-total 
			font-size: 16px
			font-weight: 500
			padding: 20px
			display: flex
			padding-bottom: 10px
			justify-content: space-between
			align-items: center
			span 
				font-size: 18px
				color: $red
	.address
		background-color: $white
		border-radius: 15px
		.content
			padding: 20px
			.info
				font-size: 14px
				padding-bottom: 10px
				@media(max-width: 576px)
					font-size: 14px
				
				strong
					text-transform: uppercase
					

	.button
		padding: 30px 0
		text-align: right
		.btn-qly
			background-color: $blue
			padding: 10px 40px
			color: $white
			text-transform: uppercase
			font-weight: 600
			
			border-radius: 0 0 39px 39px
			font-size: 14px
			position: relative
			&::before
				position: absolute
				content: "\EA6E"
				font-family: "remixicon"
				top: 50%
				right: 10px
				transform: translateY(-50%)
