footer
	background-color: #1e4488
	color: $white
	
	padding-bottom: 20px
	.footer-top
		border-bottom: 1px solid #00225e
	.border-right
		border-right: 1px solid #00225e
		@media screen and (max-width: 780px)
			.border-right
				border-right: none
	.footer-list
		
		height: 100%
		padding: 40px 0 40px 20px
		display: flex
		flex-direction: column
		
	
	.title
		font-size: 18px
	.info
		font-size: 14px
		line-height: 1.8
		padding-top: 20px
	.button
		background-color: $red
		padding: 10px 20px
		border-radius: 5px
		font-size: 14px
		font-weight: bold
		text-transform: uppercase
		margin-top: 20px
		text-align: center
		img
			padding-left: 20px
	ul
		list-style: none
		padding: 0
		li
			font-size: 14px
			line-height: 1.8
			a
				+hover-underline(95%, #2d88ff)
	.list
		padding: 10px 0
	.logo_ft
		margin: auto
		padding-bottom: 20px
	.list3
		display: flex
		li
			display: flex
			flex-direction: row
	.list2
		display: flex
		flex-wrap: wrap 
		// flex-direction: row
		// ul
		max-width: 350px
		li
			display: flex
			flex-direction: row
			flex-wrap: wrap
			padding: 10px 20px 20px 10px

	.fb
		width: 100%
		iframe
			width: 100%
	.fl
		display: flex
		justify-content: flex-end
		margin-top: 20px
		@media(max-width: 576px)
			margin-right: 30px
	.footer-bottom
		display: flex
		padding-top: 20px
		justify-content: space-between
		align-items: center
		@media screen and ( max-width: 1000px)
			flex-direction: column
		.left
			p
				font-size: 13px
				padding-bottom: 10px
			.text-ft
				font-size: 14px
				font-weight: bold
				padding-bottom: 10px
		.right
			ul
				display: flex
			
				li
					padding: 20px
					position: relative
					&::before
						content: ""
						width: 1px
						height: 14px
						background-color: $white
						position: absolute
						right: 0
						top: 50%
						transform: translateY(-50%)
					&:last-child
						padding-right: 0
						&::before
							display: none
		.luotxem 
			display: flex
			width: 100%
			justify-content: flex-end
			p
				font-size: 13px
			
