.header-index5
	padding-bottom: 30px
	padding-top: 30px
	.slide
		position: relative
		padding-bottom: 30px
		.product-item
			margin: 5px
			
	.button-next,.button-prev 
		width: 50px
		height: 50px
		border-radius: 50%
		border: 1px solid #cccccc
		display: flex
		justify-content: center
		align-items: center
		cursor: pointer
		background-color: #fff
		&:hover
			background-color: $red
			i
				color: $white

		i
			color: #cccccc
			font-size: 24px
	.button-next
		position: absolute
		top: 50%
		transform: translateY(-50%)
		right: -90px
		z-index: 11
	.button-prev
		position: absolute
		top: 50%
		transform: translateY(-50%)
		left: -90px
		z-index: 11
	@media screen and ( max-width: 1366px )
		.button-next
			right: -30px
		.button-prev 
			left: -30px
	@media screen and ( max-width: 1280px )
		.button-next, .button-prev
			position: static 
			top: auto 
			right: auto 
			left: auto
			bottom: auto 
			transform: none 
			margin: 0 10px
		.main-button 
			display: flex
			justify-content: center
			align-items: center
			margin-top: 20px

.bg-light-blue
	background-color: #f3f6fb
