header 
	// display: none
	position: fixed
	z-index: 999
	width: 100%
	background-color: $white
	left: 0
	right: 0
	top: 0

	
	
	.header-wrap
		display: flex
		justify-content: space-between
		align-items: center
	.head-right
		display: flex
		align-items: center
	
		.info
			display: flex
			font-size: 12px
			li
				display: flex
				border-right: 1px solid #e1e1e1
				border-left: 1px solid #e1e1e1
				padding: 20px
				i
					font-size: 14px
					color: $blue
				p
					padding-left: 10px
		.login
			display: flex
			align-items: center
			
			border-right: 1px solid #cccccc
			padding: 20px
			@media(max-width: 1280px)
				border-right: none
				
			i 
				font-size: 14px
				color: $blue
				padding-right: 10px
			a
				font-size: 12px
		.cart
			display: flex
			padding: 20px
			border-right: 1px solid #cccccc
			@media(max-width: 1280px)
				border-right: none
				padding-right: 0
			i
				font-size: 14px
				padding-right: 10px
			span
				padding: 2px 6px
				background-color: $red
				border-radius: 50%
				margin: auto
				color: white
				font-size: 12px
		.language
			padding: 20px
			border-right: 1px solid #cccccc
			@media(max-width: 1280px)
				border-right: none
				padding-right: 0

	.navbar
		background-color: $blue
		display: flex
		align-items: center
		ul
			display: flex
			list-style: none
			padding: 0
			li
				font-size: 12px
				text-transform: uppercase
				padding: 15px
				color: $white
				display: flex
				align-items: center
				justify-content: center
				cursor: pointer
				i
					color: $white
					padding-right: 10px
				img
					padding-right: 10px
					padding-bottom: 5px
					
					
				&:hover
					color: $blue
					font-weight: 600
					background-color: $white
					border-bottom: 2px solid $blue2
					i
						color: $blue
				&.active
					background-color: $red
					color: $white
					i
						color: $white
					
		@media(max-width: 1280px)
			display: none
	.searchbox 
		margin-top: 5px
		position: relative
		max-width: 350px
		input[type='text']
			border-radius: 25px
			border: none
			outline: none
			padding: 10px
			&::placeholder
				text-transform: capitalize
				font-size: 14px
				color: #999999
		button
			background: none
			border: none
			position: absolute
			right: 14px
			top: 6px
			img
				width: 20px
				height: 20px
	// responsive
	// .button-hamburger
	// 	display: none 
	// 	@media screen and ( max-width: 1280px)
	// 		display: block
	// @media screen and ( max-width: 1280px )
	// 	.head-right
	// 		display: none
		
	.button-hamburger
		display: none
		@media(max-width: 1280px)
			display: block
	.mobile-wrap 
		display: none
		background-color: $blue
		.button-close 
			text-align: right
			padding-right: 20px
			padding-top: 20px
			em
				background-color: $red
				color: $white
		@media(max-width: 1280px)
			display: block
			transform: translateX(-100%)
			transition: .4s ease-in-out all
			position: fixed
			top: 80px
			bottom: 0
			left: 0
			z-index: 111
			width: 100vw
			&.active
				transform: translateX(0)
			.main-menu 
				list-style: none
				li
					display: flex
					flex-direction: column
					padding: 10px 0
					i
						color: #58b2fe
						font-size: 20px
					a
						text-transform: uppercase
						color: $white
						&:hover
							color: #58b2fe
					.searchbox
						input[type="text"]
							background-color: $blue
			.info
				list-style: none
				border-top: 1px solid #cccccc
				padding: 20px
				li
					display: flex
					padding: 10px
					i
						padding-right: 20px
						color: $white
					p
						color: $white
			.login
				display: flex
				padding-left: 25px
				padding-bottom: 20px
				
				i
					display: none
				a
					padding: 10px
					background-color: $white
					color: $blue
					margin-right: 10px
					font-weight: bold
					border-radius: 50px
					&:hover
						color: $red
			.searchbox
				padding-right: 20px
				padding-bottom: 20px
				input
					position: relative
					width: 100%
				button
					position: absolute
					top: 35%
					right: 20px
					transform: translateY(-50%)
					i
						font-size: 20px
						color: $blue
						
	@media(max-width: 1280px)
		.button-hamburger
			padding: 20px 
			margin-top: 10px
			color: $blue
				
