// 	h1.title-main Sản phẩm bán chạy
.title-global
	display: flex
	flex-direction: column
	align-items: center
	text-transform: uppercase
	padding-bottom: 30px
	.title-main
		color: $blue
		font-weight: 500
		font-size: 36px
		@media screen and ( max-width: 680px)
			font-size: 24px
.bg-red
	background-color: $red
.bg-blue
	background-color: $blue
.type-none 
	list-style-type: none 
	margin: 0
	padding: 0

.product-item
	box-shadow: 0 0 5px 0 rgba(#000000, 0.2)
	border-radius: 10px
	display: flex
	align-items: center
	flex-direction: column
	padding-bottom: 20px
	margin-bottom: 30px
	
	.sale
		
		ul
			display: flex
			align-items: center
			justify-content: center
			li
				display: flex
				margin-left: 20px
				padding: 20px 12px
				color: $white
				font-size: 12px
				text-transform: uppercase
				font-weight: bold
				border-radius: 0 0 50px 50px
				&:first-child
					margin-left: 0
	.img
		padding: 20px 0
		width: 100%
		height: 170px
		display: flex
		justify-content: center
		object-fit: cover
		transition: .2s ease-in-out all
		&:hover
			transform: scale(1.1)

	.add-to-cart 
		display: flex
		background-color: #f8f8f8
		padding: 8px 20px
		border-radius: 50px
		align-items: center

		i
			color: $blue
			padding-right: 10px
		p
			font-size: 14px
			color: #999999
		&:hover
			background-color: $red

			i
				color: $white
				
			p
				
				color: $white

	.title-sub
		font-size: 15px
		justify-content: center
		display: flex
		font-weight: bold
		padding-top: 20px

		&:hover
			color: $blue
	.price-wrap
		padding-top: 10px
		display: flex
		justify-content: center
		.price
			font-size: 15px
			color: $red
			font-weight: bold
		.old-price
			font-size: 14px
			text-decoration: line-through
			color: #999999
			padding-left: 10px
	.review
		display: flex
		align-items: center
		ul
			display: flex
			li
				i
					color:#ffc120
					font-size: 12px
		span 
			font-size: 10px
		.count-star
			padding: 0 10px
			border-right: 1px solid #999999
		.count-buy
			padding-left: 10px
	.info-top
		background-color: $white
		box-shadow: 0 0 10px 0 rgba(#000000, 0.2)
		width: 100%
		position: relative
		padding-bottom: 30px
		.icon-small
			width: 40px
			height: 40px
			position: absolute
			left: 50%
			bottom: -20px
			transform: translateX(-50%)
			display: flex
			justify-content: center
			border: 1px solid #aba9a9

			i
				padding: 0
	.info
		width: 100%
		float: left
		justify-content: center
		align-items: center
.none-shadow
	box-shadow: none

.tab-global
	
	ul
		display: flex
		align-items: center
		justify-content: center
		li
			display: flex
			padding-left: 30px
			text-align: center
			&:first-child
				padding-left: 0
			@media(max-width: 576px)
				padding: 0
			

	.btn
		font-size: 16px
		padding: 10px 20px
		border: 1px solid $blue
		border-radius: 0 0 39px 39px
		width: 185px
		height: 40px
		&:hover
			border: 1px solid $red
			color: $red
			font-weight: bold
		&.active
			border: 1px solid $red
			color: $red
			font-weight: bold
		&-border-white
			border: 1px solid $white
			color: $white
			transition: all .5s
			text-transform: uppercase
			font-size: 14px
			&:hover
				background-color: $blue
				color: $white
				border: solid 1px transparent
		
		&-bg
			background-color: $blue
			color: $white
			font-size: 14px
			text-transform: uppercase
			&:hover
				background-color: $red
				color: $white


.backtotop
	position: fixed
	right: 5%
	bottom: 10%
	z-index: 9999
	width: 50px
	height: 50px
	border: 1px solid white
	display: flex
	align-items: center
	justify-content: center
	background-color: $blue
	border-radius: 50%
	em
		color: #fff
		font-weight: bold
		font-size: 24px
		
