.cart-page.cart-step-1
	background-color: #f3f6fb
	padding-bottom: 30px 
	margin-top: 30px
	// @media(max-width: 768px)
	// 	margin-top: 30px
	@media(max-width: 576px)
		margin-top: 60px

	.carttitle
		font-size: 24px
		color: $blue
		border-bottom: 1px solid #cccccc
		padding-bottom: 10px
		@media(max-width: 576px)
			font-size: 20px
		
	.cart-wrap
		background-color: #ffffff
		padding: 20px
		
	.body-cart
		max-height: 530px
		overflow-x: hidden
		overflow-y: auto
		&::-webkit-scrollbar
			width: 6px
			cursor: pointer
			height: 150px
		&::-webkit-scrollbar-track 
			background: #f1f1f1 
		

		/* Handle */
		&::-webkit-scrollbar-thumb 
			background: $blue
		

		/* Handle on hover */
		&::-webkit-scrollbar-thumb:hover 
			opacity: 0.8
		

	.cart-table
		display: flex
		justify-content: space-between
		width: 100%
		
		tbody
			width: 100%
			tr
				display: flex
				justify-content: space-between
				align-items: center
				width: 100%
				padding: 15px
				border-bottom: 1px solid #cccccc
				flex-wrap: wrap
	.product
		@media(max-width: 1024px)
			flex: 0 0 100%
			.product-name
				a
					font-size: 14px
		figure
			display: flex
			align-items: center
			.product-img
				width: 95px
				min-width: 95px
				height: 95px
				img 
					width: 100%
			.product-name
				font-size: 16px
				@media(max-width: 576px)
					font-size: 14px

			.removeproduct
				font-size: 14px
				display: flex
				color: #cccccc
				em
					padding-right: 10px
	.amount
		@media(max-width: 1024px)
			flex: 0 0 50%
			
			padding: 20px 0
		@media(max-width: 576px)
			flex: 0 0 100%
			display: flex
			justify-content: center
			
		.input-quality
			display: flex
			align-items: center
			justify-content: center
			border-radius: 0 0 39px 39px
			overflow: hidden
			max-width: 130px
			border: 1px solid #cccccc
			height: 40px
			.btn-desc, .btn-inc
				width: 33px
				height: 100%
				background-color: #f8f8f8
				// border-radius: 0 0px 10px 10px
				display: flex
				justify-content: center
				align-items: center
				min-width: 33px
			input
				width: 100%
				border: none
				outline: none
				height: 100%
				text-align: center
				padding: 5px
	.price
		@media(max-width: 576px)
			margin: 0 auto
	
			
			
		.top
			display: flex	
			align-items: center
			.old
				font-size: 12px
				text-decoration: line-through
				border-right: 1px solid #cccccc
				padding: 0 10px
			.sale
				font-size: 12px
				color: $red
				padding: 0 10px
			.new
				font-size: 16px
		.mid
			font-size: 14px
			text-align: right
		.bottom
			font-size: 16px
			color: $blue
			font-weight: bold
			text-align: right


.cart-step-1-bottom
	padding-top: 30px

	.btn-wrap	
		display: flex
		justify-content: space-between
		.btn-left
			padding-left: 20px
			@media(max-width: 576px)
				display: none
			.btn-back
				font-size: 14px
				text-transform: uppercase
				font-weight: bold
				position: relative
				&::before
					content: "\EA64"
					position: absolute
					font-family: "remixicon"
					top: 50%
					transform: translateY(-50%)
					left: -20px
					font-size: 14px
		.btn-right
			display: flex
			flex-direction: column
			justify-content: space-between
			@media(max-width: 576px)
				margin: 0 auto
				
			.totalamount
				font-size: 20px
				font-weight: 600
				strong
					color: $red
					padding-left: 20px
			.payment-des
				font-size: 14px
				text-align: right
			.btn-continue
				background-color: $blue
				padding: 10px 20px
				color: $white
				text-transform: uppercase
				font-weight: 600
				margin-top: 20px
				border-radius: 0 0 39px 39px
				font-size: 14px
				position: relative
				&::before
					position: absolute
					content: "\EA6E"
					font-family: "remixicon"
					top: 50%
					right: 20px
					transform: translateY(-50%)


